import { useState } from "react";
import { Container } from "./components/Container";
import { Input } from "./components/Input";
import "./styles.css";

export default function App() {
  function Parse(horario) {
    let [hour, minute] = horario.split(":").map((v) => parseInt(v));
    if (!minute) {
      minute = 0;
    }
    return hour * 60 + minute;
  }

  function Duration(entry, said) {
    return Parse(entry) - Parse(said);
  }
  const [entrada, setEntrada] = useState("");

  const [saida, setSaida] = useState("");

  const value = Duration(entrada, saida);

  let diff = Math.abs(value);

  let precoFinal;

  if (diff !== 0) {
    let horas = Math.floor(diff / 60);
    let minutos = diff - horas * 60;
    let valorEntrada = 2;
    let precoPrimeiraHora = 3;
    let valorHoraExtra = 4;

    if ((horas > Math.floor(60 / 60)) & (minutos >= 0)) {
      precoFinal =
        valorEntrada + precoPrimeiraHora + (horas - 1) * valorHoraExtra;
    } else {
      precoFinal = valorEntrada + precoPrimeiraHora;
    }
  }

  return (
    <div className="App">
      <Container>
        <h1>Estacionamento</h1>
        <label>Hora da Entrada</label>
        <Input value={entrada} onChange={(e) => setEntrada(e.target.value)} />
        <label>Hora da Saida</label>
        <Input value={saida} onChange={(e) => setSaida(e.target.value)} />
        <h2> valor do estacionamento</h2>
        <h3>R$ {`${precoFinal}`}</h3>
      </Container>
    </div>
  );
}
